const playClick = new CustomEvent('play_click');
const favoriteClick = new CustomEvent('favorite_click');

class SongItem extends HTMLElement {

    // observedAttributes est un getter statique qui retourne un tableau des noms d'attributs √† observer.
    // Lorsque l'un de ces attributs change de valeur, attributeChangedCallback est appel√©.
    static observedAttributes = ['favorite', 'playpause'];

    // connectedCallback est une m√©thode du cycle de vie qui est appel√©e lorsque l'√©l√©ment est ins√©r√© dans le DOM.
    // C'est l'endroit id√©al pour configurer l'initialisation, attacher des √©v√©nements, ou effectuer le rendu.
    connectedCallback() {
        this.render();
    }

    // attributeChangedCallback est appel√© automatiquement lorsque les attributs list√©s dans observedAttributes changent.
    // Ici, il appelle simplement render pour mettre √† jour l'√©l√©ment avec le nouvel √©tat des attributs.
    attributeChangedCallback() {
        this.render();
    }

    render() {
        const favoriteIcon = this.getAttribute('favorite') == 'true' ? 'favorite' : 'favorite_border';
        const playIcon = this.getAttribute('playpause') == 'true' ? 'pause' : 'play_arrow';

        this.innerHTML =
            `<a href="${this.getAttribute('href')}">
            <div class="list-item-title">${this.getAttribute('title')}</div>
            <div class="list-item-actions">
                <button type="button" class="icon-button favorite-button ">
                    <span class="material-icons">${favoriteIcon}</span>
                </button>
                <button type="button" class="icon-button play-button">
                    <span class="material-icons">${playIcon}</span>
                </button>
            </div>
        </a>`

        // Attache des gestionnaires d'√©v√©nements aux boutons pour dispatcher des √©v√©nements personnalis√©s lorsque ceux-ci sont cliqu√©s.
        this.querySelector('.play-button').addEventListener('click', (e) => {
            e.preventDefault();
            this.dispatchEvent(playClick);
        })

        this.querySelector('.favorite-button').addEventListener('click', (e) => {
            e.preventDefault();
            this.dispatchEvent(favoriteClick);
        })
    }
}

customElements.define('song-item', SongItem);